import { Injectable } from "@angular/core";
import { HttpInterceptor, HttpClient, HttpRequest, HttpHandler, HttpEvent, HttpErrorResponse } from '@angular/common/http';
import { Storage } from '@ionic/storage';
import { Observable, throwError } from 'rxjs';
import { AuthenticationService } from '../services/authentication.service';
import { catchError } from 'rxjs/operators';
import { ToastController } from '@ionic/angular';

@Injectable()
export class JwtInterceptor implements HttpInterceptor {

    currentUser:any;
    
     intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>>{
         

         request = request.clone({
             setHeaders:{
                'Content-Type': 'application/json',
             }
         });

         this.getCurrentUser();
         if(this.currentUser && this.currentUser.token){
             request = request.clone({
                 setHeaders:{
                    Authorization: `Bearer ${this.currentUser.token}`
                 }
             })
         }
         return next.handle(request)
         .pipe(
             catchError(this.handleError)
         );

     }

    constructor(public toastController : ToastController ,public http: HttpClient, private storage:Storage, private authService: AuthenticationService){
    this.currentUser = this.authService.currentUserValue();
    
    }

    handleError(error: HttpErrorResponse){
        console.log("lalalalalalalala");
        this.presentToast();
        return throwError(error);
    }

    getCurrentUser()
    {
        this.currentUser = this.authService.currentUserValue();
    }

    async presentToast() {
        const toast = await this.toastController.create({
          message: 'Your settings have been saved.',
          duration: 2000
        });
        toast.present();
      }

}